import React from 'react';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { useStyles } from './experienceCardStyles'
import LinearScaleIcon from '@material-ui/icons/LinearScale';
import './experienceCardStyles_css.css'

interface ExperienceCardProps {
  position: any
}

const ExperienceCard = (props: ExperienceCardProps) => {
    const { position } = props;

    const classes = useStyles();
    const bull = <span className={classes.bullet}>•</span>;

    const companyPositions = position.positions.map((subPosition, idx: number) => {

      const details = position.positions[0].details.map((text: string, _: number) => (
        <p className={classes.details}>{bull}{text}</p>
      ));
      const divider = idx !== 0 && <LinearScaleIcon className={classes.breakIcon} />;
      return (
        <>
          {divider}
          <Typography className={classes.subtitle}>
              {subPosition.location} <br/>
              {subPosition.dates} {bull} {subPosition.duration}
          </Typography>
          <Typography variant="body2" component="p">{details}</Typography>
        </>
      )
    });

    return (
      <>
      <Card className={classes.root}>
        <CardContent>
          <Typography variant="h5" component="h2">
            {position.company}
          </Typography>
          <hr/>
          {companyPositions}
        </CardContent>
        <CardActions>
        </CardActions>
      </Card>
      <br/>
      </>
    )
};

export default ExperienceCard